.App {
  text-align: center;
}

#badges > span {
  margin:4px;
}

.input-head, #badges {
    margin: 2rem;
    vertical-align: middle;
    line-height: 3.6;
    border-bottom: 1px solid #bfbfbf;
    padding-bottom: 2rem;
}
.raw {
    border-bottom: 1px solid #bfbfbf;
    padding-bottom: 2rem;
}

#tags h2{
    text-align: center;
}

pre, #tags {
    text-align: initial;
}
.input-head button, .input-head div {
  margin:0;
}

.input-head div {
  width:500px;
}

.input-head button {
  margin-right:40px;
}

.FetchUrl {
  padding:2rem 5rem;
}

.App-header {
    background-color: #5f5b92;
    height: 150px;
    padding: 20px;
    color: white;
    -webkit-box-shadow: 0 0 10px 4px #9c9c9c;
            box-shadow: 0 0 10px 4px #9c9c9c;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



